export function build(name, call) {
  try {
    var item = document.querySelector(`#${name}`);
    dataSet[name].onAutocomplete = call;
    // eslint-disable-next-line no-undef
    M.Autocomplete.init(item, dataSet[name]);
  } catch (e) {
    console.log(e);
  }
}
